.navbarWrap {
  background-image: linear-gradient( 109.6deg,  rgba(255,179,71,1) 11.2%, rgba(255,155,57,1) 91.1% );
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.nav {
  grid-area: n;
  display: grid;
  grid-template-rows: repeat(5, 30px);
  padding: 40px 10px;
  grid-template-columns: 1fr;
  align-items: center;
}

.item {
  cursor: pointer;
}

.item a{
  text-decoration: none;
  color: var(--black);
}

.item .active{
  color: #0055ff;
}
