.login__form {
  padding-top: 45px;
  display: grid;
  grid-template-columns: 250px;
  grid-template-rows: repeat(4, 25px) minmax(25px, 50px);
  grid-gap: 10px;
}

.commonError {
  color: red;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 5px;
  font-weight: bold;
  font-size: 0.8em;
}
