.content {
  grid-area: c;
  background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(137,210,253,1) 0%, rgba(255,241,188,1) 90% );
  display: grid;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.banner {
  width: 100%;
}

.info {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 16px;
}
.contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
