:root {
  --yellow: rgba(255,241,53,1);
  --black: #232323;
  --fullblack: #000;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.app-wrapper {
  margin: 0 auto;
  display: grid;
  max-width: 1200px;
  background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,12,253,1) 0%, rgba(255,241,53,1) 90% );
  width: 100%;
  grid-template-areas:
  "h h"
  "n c";
  grid-template-rows: 120px 1fr;
  grid-template-columns: minmax(150px, 2fr) 10fr;
  grid-row-gap: 0;
  grid-column-gap: 20px;
}

.app-wrapper-content {
  grid-area: "c";
}
